body {
  margin: 0;
  font-family: Roboto, sans-serif;
}

.card {
  background-image: url("imac.202c52ff.jpeg");
  background-position: 19px 32px;
  background-repeat: no-repeat;
  background-size: 160px, 134px;
  border: 1px solid #f3f3f3;
  width: 200px;
  height: 408px;
}

.card__text {
  color: #060b35;
  margin-top: 206px;
  padding: 0 16px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__prod-code {
  color: #616070;
