  font-size: 12px;
  font-weight: 400;
  line-height: 18px;
  display: flex;
}

.card__price-value {
  color: #000;
  margin-left: 85px;
  font-size: 16px;
  font-weight: 700;
}

.card__button-buy {
  color: #fff;
  text-align: center;
  background-color: #00acdc;
  border-width: 0;
